// Здесь находятся все глобальные стили проекта, например блоки по БЭМу
.main-title {
  @include adaptive-value('font-size', 40, 30, 1);
  font-weight: 800;
  line-height: calc(34 / 40);
}

// WEBP or JPG

.no-webp {
  .advantages {
    &__swiper-slide {
      &:nth-child(1) {
        background-image: url("../images/bg-01.jpg");
      }
      &:nth-child(2) {
        background-image: url("../images/bg-02.jpg");
      }
      &:nth-child(3) {
        background-image: url("../images/bg-03.jpg");
      }
      &:nth-child(4) {
        background-image: url("../images/bg-04.jpg");
      }
    }
  }
}

.webp {
  .advantages {
    &__swiper-slide {
      &:nth-child(1) {
        background-image: url("../images/bg-01.webp");
      }
      &:nth-child(2) {
        background-image: url("../images/bg-02.webp");
      }
      &:nth-child(3) {
        background-image: url("../images/bg-03.webp");
      }
      &:nth-child(4) {
        background-image: url("../images/bg-04.webp");
      }
    }
  }
}
